.logincontainer{
    height: 100%;
    text-align: center;
    
    padding: 15px 15px 15px 0px;
    
}

.login{
    margin: 0 auto;
  width: 600px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0  0  70px rgba(0, 0, 0, 0.1);
  margin-top: 10px;
  margin-bottom: 10px;
 
}



.login h2{
    font-size: 35px;
    font-weight: bold;
    color: #AA0601;
    margin-top: 30px;
    margin-bottom: 20px;
}

.inline-icon-input {
    padding-left: 50px;
    border: none;
    border-bottom: 1px solid #4f4f4f;
    border-radius: 0;
    margin-bottom: 20px;
}





.fa-envelope:before{
    color: #AA0601;
    font-size: 20px;
}

.fa-lock:before{
    color: #AA0601;
    font-size: 20px;
}
input:focus{
    outline-color:#d8817e;

}
.username{
    font-size: 20px;
    width: 250px;
    height: 30px;
    margin: 10px;
    margin-bottom: 20px;
    border: none;
    border-bottom: 1px solid #4f4f4f;
    
    
}
.password{
    font-size: 20px;
    width: 250px;
    height: 30px;
    margin: 10px;
    margin-bottom: 20px;
    border: none;
    border-bottom: 1px solid #4f4f4f;
    margin-bottom: 200px;
}

.submit{
    font-size: 20px;
    line-height: 30px;
    color: white;  
    background: #AA0601;
    width: 250px;
    height: 35px;
    margin: 10px;
    padding: 5px;
    border-radius: 5px;
    border: 0px;
    margin-bottom: 20px;
  }

.submit:hover{
   opacity: 0.8;
  }

  @media(max-width:992px){
    .login{
        width: 400px;  
    }
 
}


@media(max-width:767px){


    .login{
        width: 350px;
           
    }



   
}

@media(max-width:576px){
    .login h2{
        font-size: 30px;
        font-weight: bold;
    
        margin-top: 30px;
        margin-bottom: 20px;
    }

    .username{     
        width: 180px;      
        margin-bottom: 20px;        
    }

    .password{
        width: 180px;  
        margin-bottom: 30px;
    }
        
    .login{
        width: 250px;     
    }

    .submit{      
        width: 200px;   
    }
        

}
